.graphic-design-main-container {
  display: flex;
  flex-direction: row;
  flex-direction: column;
  background-color: #e5e5e5;
}

.graphic-design-main-container-row-1 {
  display: flex;
  flex-direction: row;
}

.graphic-design-main-container-column-1-row-1-column-1 {
  flex: 1;
  display: flex;
  flex-direction: column;
  gap: 7.5vh;
  padding: 4vh 9vw;
}

.graphic-design-main-container-column-1-row-1-column-1-card-1 {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  border-radius: 25px;
  width: 80%;
  padding: 3vh 2vw;
  gap: 3vh;
  align-self: flex-start;
  position: relative;
  background-color: white;
  box-shadow: 0 0 10px 2px rgba(0, 0, 0, 0.4);
  cursor: pointer;
  transition: all 0.5s ease-in;
  text-decoration: none;
  color: black;
}

.graphic-design-main-container-column-1-row-1-column-1-card-1 h1 {
  font-size: 2vw;
}

.graphic-design-main-container-column-1-row-1-column-1-card-1 p {
  text-align: center;
  line-height: 2;
  font-size: 1vw;
  z-index: 5;
}

