.left-arrow {
  position: absolute;
  top: 27.5vh;
  /* top: 125%; */
  left: -6vw; /* Adjust the value according to your preference */
  transform: rotate(10deg);
  /* transform: translateY(-50%); */
  height: 27.5vw;
}

.right-arrow {
  position: absolute;
  top: 27.5vh;
  /* top: 125%; */
  right: -6vw; /* Adjust the value according to your preference */
  transform: rotate(-10deg);
  /* transform: translateY(-50%); */
  /* width: 10rem; */
  height: 27.5vw;
}

.graphics-triangle-1{
  position: absolute;
  top: 2.5vh;
  left: -5vw; /* Adjust the value according to your preference */
  transform: translateY(-50%);
  width: 3vw;
  cursor: default;
}

.graphics-triangle-2{
  position: absolute;
  top: 5vh;
  right: -5.5vw; /* Adjust the value according to your preference */
  transform: translateY(-50%);
  width: 3vw;
  cursor: default;
}

