.graphic-designing-main-description {
  display: flex;
  flex-direction: column;
  padding: 5vh 3vw;
  gap: 2.5vh;
  justify-content: center;
  align-items: center;
  background-color: #e5e5e5;
}

.graphic-designing-main-description h1 {
  text-align: center;
}

.graphic-designing-main-description p {
  text-align: center;
  padding: 0 2vw;
  line-height: 1.75;
}

.graphic-designing-main-description button {
  background-color: #fff;
  font-size: 1.25rem;
  color: #693897;
  width: fit-content;
  height: fit-content;
  border-radius: 10px;
  padding: 1% 1%;
  margin-top: 1%;
  transition: all 0.25s;
  border: none;
}

.graphic-designing-main-description button:hover {
  cursor: pointer;
  scale: 1.2;
  box-shadow: 0 0 10px 5px #e9d9d9;
}